/*
Josh's Custom CSS Reset
https://www.joshwcomeau.com/css/custom-css-reset/
*/
*,
*::before,
*::after {
  box-sizing: border-box;
}

html,
body {
  height: 100%;
}

body {
  -webkit-font-smoothing: antialiased;
}

img,
picture,
video,
canvas,
svg {
  display: block;
  max-width: 100%;
  height: auto;
}

input,
button,
textarea,
select {
  font: inherit;
}

p,
h1,
h2,
h3,
h4,
h5,
h6 {
  overflow-wrap: break-word;
}

small {
  font-size: var(--wp--preset--font-size--small);
}

ul,
ol {
  padding: 0 0 0 var(--wp--preset--spacing--50);
}

.visually-hidden:not(:focus):not(:active):not(:focus):not(:active) {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

.always-visually-hidden {
  border: 0;
  clip: rect(0 0 0 0);
  height: auto;
  margin: 0;
  overflow: hidden;
  padding: 0;
  position: absolute;
  width: 1px;
  white-space: nowrap;
}

input[type=submit] {
  appearance: none;
  background-color: var(--wp--preset--color--primary);
  border-radius: 0px;
  border-color: var(--wp--preset--color--background);
  border-style: solid;
  border-width: 2px;
  color: var(--wp--preset--color--background);
  padding-top: var(--wp--preset--spacing--20);
  padding-right: var(--wp--preset--spacing--50);
  padding-bottom: var(--wp--preset--spacing--20);
  padding-left: var(--wp--preset--spacing--50);
  text-decoration: none;
}

.wp-site-blocks > header {
  z-index: 999;
  margin: 0 auto;
  border-bottom: 1px solid rgba(159, 123, 78, 0.55);
  width: 100%;
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  background-color: rgba(0, 0, 0, 0.55);
}

body.admin-bar header {
  top: 32px;
}

main,
.woocommerce div.product {
  margin-top: 9em;
}

.wp-block-group {
  padding-bottom: 0;
  padding-top: 0;
}
.wp-block-group.is-selected, .wp-block-group.has-child-selected {
  padding-bottom: 1.25em;
}

.wp-block-quote {
  border-left: 2px solid var(--wp--preset--color--primary);
  padding-left: var(--wp--preset--spacing--50);
}